<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Data Leaderboard</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              @click="getData()"
              class="button bg-theme-1 hover:bg-primary text-white flex"
            >
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-8 gap-2 mb-3">
          <div class="flex flex-col col-span-6 md:col-span-2">
            <p
              class="text-base mr-auto mb-3 md:mb-0"
            >
              Filter :
            </p>
            <div class="w-full flex flex-row">
              <Litepicker
                v-model="dataLB.dateRange"
                :options="{
                  autoApply: false,
                  singleMode: false,
                  numberOfColumns: 2,
                  numberOfMonths: 2,
                  showWeekNumbers: true,
                  dropdowns: {
                    minYear: 1990,
                    maxYear: null,
                    months: true,
                    years: true,
                  },
                }"
                class="input w-56 border block"
              />
            </div>
          </div>
          <div class="flex flex-col col-span-6 md:col-span-2">
            <p
              class="text-base mr-auto mb-3 md:mb-0"
            >
              Tipe :
            </p>
            <div class="w-full flex flex-row">
              <TailSelect
                v-model="dataLB.tipe"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(k, idx) in listTipe"
                  v-bind:key="idx"
                  :value="k.value"
                >
                  {{ k.title }}
                </option>
              </TailSelect>
              <button
                @click="getData()"
                class="button bg-theme-1 hover:bg-primary text-white w-24 ml-auto mt-2 md:ml-2 md:mt-auto mb-1"
              >
                View
              </button>
            </div>
          </div>
          <div class="w-full col-span-6 md:col-span-4">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
                v-model="dataLB.search"
                v-on:keyup.enter="getData()"
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataLB.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="w-12 h-12 rounded-full overflow-hidden shadow-lg image-fit zoom-in">
                    <img class="w-full" :src="u.photo" alt="photo-user" :title="'Photo ' + u.name" data-action="zoom">
                  </div>
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.name }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.jumlah }}
                </td>
              </tr>
            </tbody>
          </table>
          <div
            v-if="dataLB.data.length === 0"
            class="w-full p-5 flex items-center justify-center"
          >
            <p class="text-base font-bold">Tidak Ada Data Leaderboard.</p>
          </div>
          <LoadingDiv id="loadingDataLB" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        v-if="dataLB.data.length !== 0"
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li v-if="dataLB.page !== 1" @click="goToPage(1)">
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-if="dataLB.page !== 1" @click="beforePage()">
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li v-for="(p, idx) in dataLB.paginate.pages" :key="idx">
            <span
              :class="[
                'pagination__link cursor-pointer',
                dataLB.page === p ? 'pagination__link--active' : '',
              ]"
              @click="goToPage(p)"
              >{{ p }}</span
            >
          </li>
          <li
            v-if="dataLB.page !== dataLB.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataLB.page !== dataLB.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataLB.pageSize"
          v-on:change="changePS()"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>

  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { helper as $h} from "@/utils/helper";
import { api } from "@/utils/api-config";
import axios from "axios";
import { dataTipeLB } from "./leaderboard";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listTipe = dataTipeLB();

    const titleTable: Array<string> = [
      "Photo",
      "Name",
      "Jumlah",
    ];

    const dataLB = reactive({
      tempData: [] as any,
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      tipe: "affiliate",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
        dataPaginate: [],
      } as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataLB.dateRange = `${$h.formatDate(firstDay.toString(), 'DD MMM, YYYY')} - ${$h.formatDate(lastDay.toString(), 'DD MMM, YYYY')}`;

    const getDataPagination = async (pg?:1) => {
      const page = pg !== 1 ? dataLB.page : pg;
      const pageSize = dataLB.pageSize;
      dataLB.paginate = $h.paginationLocal(dataLB.tempData.length, page, pageSize, 3, dataLB.tempData);
      dataLB.data = dataLB.paginate.dataPaginate;
    };

    const getData = async () => {
      cash("#loadingDataLB").show();

      const since = $h.formatDate(dataLB.dateRange.split('-')[0], 'YYYY-MM-DD');
      const until = $h.formatDate(dataLB.dateRange.split('-')[1], 'YYYY-MM-DD');
      const tipe = dataLB.tipe;

      axios
        .get(
          api.affLeaderboard +
          "?since=" +
          since +
          "&until=" +
          until +
          "&tipe=" +
          tipe
          , configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataLB.tempData = rs;
          await getDataPagination(1);
          // console.log("dataLB.paginate ", dataLB.paginate);
          cash("#loadingDataLB").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDataLB").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleJwtExpired(error);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getDataPagination();
    };

    const goToPage = (p: number) => {
      dataLB.page = p;
      getDataPagination();
    };

    const nextPage = () => {
      dataLB.page = dataLB.page + 1;
      getDataPagination();
    };

    const beforePage = () => {
      dataLB.page = dataLB.page - 1;
      getDataPagination();
    };

    const lastPage = () => {
      dataLB.page = dataLB.paginate.totalPages;
      getDataPagination();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataLB,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listTipe,
    };
  },
  data() {
    return {};
  },
  methods: {
  },
});
</script>
