
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";
import { dataStatus, dataPayType, dataPayStatus } from "./data-pemasukan";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import { helper as $h } from "@/utils/helper";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const router = useRouter();
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listStatus = dataStatus();
    const listPayType = dataPayType();
    const listPayStatus = dataPayStatus();

    const titleTable: Array<string> = [
      "Invoice",
      "Member",
      "Price",
      "Referal",
      "Paket",
      "Payment Type / <br> Payment Status",
      "Created At / <br> Accepted At",
      "Status",
      "#",
    ];

    const dataDP = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataDP.dateRange = `${$h.formatDate(firstDay.toString(), 'DD MMM, YYYY')} - ${$h.formatDate(lastDay.toString(), 'DD MMM, YYYY')}`;

    const getData = async () => {
      cash("#loadingDPadmin").show();

      // console.log("dateRange ", dataDP.dateRange);
      // console.log("firstDay lastDay ", firstDay, lastDay);

      const page = dataDP.page;
      const pageSize = dataDP.pageSize;
      const search = dataDP.search;
      const since = $h.formatDate(dataDP.dateRange.split('-')[0], 'YYYY-MM-DD');
      const until = $h.formatDate(dataDP.dateRange.split('-')[1], 'YYYY-MM-DD');
      axios
        .get(
          api.adminPemasukan +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize +
            "&since=" +
            since +
            "&until=" +
            until,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataDP.data = rs.map((e: any) => {
            e.price = gpf.convertToCurrency(e.price, "IDR ");
            return e;
          });
          dataDP.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataDP.paginate ", dataDP.paginate);
          cash("#loadingDPadmin").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDPadmin").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataDP.page = p;
      getData();
    };

    const nextPage = () => {
      dataDP.page = dataDP.page + 1;
      getData();
    };

    const beforePage = () => {
      dataDP.page = dataDP.page - 1;
      getData();
    };

    const lastPage = () => {
      dataDP.page = dataDP.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataDP,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listStatus,
      listPayType,
      listPayStatus,
    };
  },
  data() {
    return {
      // for modal data bs
      section: "add",
      msgSuccess: "",

      detailDP: {
        invoice: "210310C0OI1D",
        price: 497000,
        uniqueCode: 0,
        paymentMethod: "Bank Transfer",
        paymentType: "cash",
        paymentStatus: "paid",
        status: "accept",
        acceptedAt: "2021-03-10 00:00:00",
        createdAt: "2021-03-10 00:00:00",
        member: {
          name: "Wisman",
          phone: "083120384299",
          email: "wismannur@gmail.com",
        },
        payment: {
          id: 4,
          code: "014",
          accountNumber: "00702283345",
          accountName: "PT Magau Jaya Digital",
          bankName: "BCA",
          paymentType: "Bank Transfer",
        },
        refferal: {
          id: 1,
          code: "admin",
          name: "Admin",
        },
        paket: {
          id: 2,
          title: "Paket Platinum",
          price: 994000,
        },
        promo: {
          id: 2,
          code: "PROMO50",
          discount: 497000,
          type: "price",
        },
        confirm: {
          nominal: 497000,
          accountNumber: "1234567890",
          accountName: "nasrullah",
          bankName: "BNI",
          attachment: "https://api.cpnsonline.id/img/dummy-img.png",
          status: "accept",
          confirmAt: "2021-03-10 00:00:00",
        },
      },

      exDataDP: {
        invoice: "210310C0OI1D",
        price: "497000",
        uniqueCode: "0",
        paymentMethod: "Bank Transfer",
        paymentType: "cash",
        paymentStatus: "paid",
        status: "accept",
        acceptedAt: "2021-03-10 00:00:00",
        createdAt: "2021-03-10 00:00:00",
        member: {
          name: "Wisman",
          phone: "083120384299",
          email: "wismannur@gmail.com",
        },
        payment: {
          id: 4,
          code: "014",
          accountNumber: "00702283345",
          accountName: "PT Magau Jaya Digital",
          bankName: "BCA",
          paymentType: "Bank Transfer",
        },
        refferal: {
          id: 1,
          code: "admin",
          name: "Admin",
        },
        paket: {
          id: 2,
          title: "Paket Platinum",
          price: 994000,
        },
        promo: {
          id: 2,
          code: "PROMO50",
          discount: 497000,
          type: "price",
        },
        confirm: {
          nominal: 497000,
          accountNumber: "1234567890",
          accountName: "nasrullah",
          bankName: "BNI",
          attachment: "https://api.cpnsonline.id/img/dummy-img.png",
          status: "accept",
          confirmAt: "2021-03-10 00:00:00",
        },
      },

      listPackage: [],
      listPayment: [],
      listPromo: [],
    };
  },
  methods: {
    async listGetPromo(): Promise<void> {
      axios
        .get(api.publicPromo + '?refid=' + this.exDataDP.refferal.id)
        .then(async (result: any) => {
          gpf.gLoading.hide();
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          this.listPromo = rs.map((e: any) => {
            const obj = {
              id: e.id,
              title: e.code,
              value: e.id,
              discount:
                e.type === "percent"
                  ? e.discount + "%"
                  : gpf.convertToCurrency(e.discount, "IDR "),
              type: e.type,
            };
            return obj;
          });

          await gpf.showModal("#modal-data-dp");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
    async listGetPayment(): Promise<void> {
      axios
        .get(api.publicPayment)
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          this.listPayment = rs;

          await this.listGetPromo();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
    async listGetPackage(): Promise<void> {
      axios
        .get(api.publicPackage)
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get js ", rs);
          this.listPackage = rs.map((e: any) => {
            const obj = {
              title: e.title,
              value: e.id,
              price: e.price,
            };
            return obj;
          });

          await this.listGetPayment();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
    editBSApi(data: any) {
      this.section = "edit";

      gpf.gLoading.show();

      axios
        .get(api.adminPemasukan + data.invoice, this.configGet)
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get dpp ", rs);
          this.exDataDP = rs;
          const ex = this.exDataDP;
          ex.paket.id = rs.paket.id.toString();
          ex.payment.id = rs.payment.id.toString();
          ex.promo.id = rs.promo.id.toString();

          await this.listGetPackage();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
    updatedBS(): void {
      gpf.gLoading.show();
      const ex = this.exDataDP;
      const body = new FormData();
      body.append("harga", ex.price);
      body.append("kodeUnik", ex.uniqueCode);
      body.append("status", ex.status);
      body.append("paymentType", ex.paymentType);
      body.append("paymentStatus", ex.paymentStatus);
      body.append("packageId", ex.paket.id.toString());
      body.append("paymentId", ex.payment.id.toString());
      body.append("promoId", ex.promo.id.toString());

      axios
        .put(api.adminPemasukan + ex.invoice, body, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          await gpf.msgHandler(true, "Good Job!", ds.message);
          await gpf.hideModal("#modal-data-dp");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
    detailBSApi(data: any): void {
      gpf.gLoading.show();

      axios
        .get(api.adminPemasukan + data.invoice, this.configGet)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get deu ", rs);

          this.detailDP = rs;

          await gpf.showModal("#modal-detail-dp");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
  },
});
