
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";
import { dataKeyDS } from "./withdrawal";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listKeyDS = dataKeyDS();

    const titleTable: Array<string> = [
      "Account Name / <br> Account Number",
      "Bank Name",
      "Amount",
      "Status",
      "Created",
    ];

    const dataAW = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
      dataSaldo: {
        komisi: 0,
        paidWithdrawal: 0,
        unpaidWithdrawal: 0,
        saldo: 0,
      } as any,
    });

    const dataSaldo = async () => {
      cash("#loadingSaldoAff").show();

      const ds = await api.getSaldo();
      dataAW.dataSaldo = ds;

      cash("#loadingSaldoAff").hide();
    };

    const getData = async () => {
      dataSaldo();
      cash("#loadingDMaff").show();

      const page = dataAW.page;
      const pageSize = dataAW.pageSize;
      const search = dataAW.search;
      axios
        .get(
          api.ownerWithdrawal +
            "?search=" +
            search +
            "&page=" +
            page +
            "&pageSize=" +
            pageSize,
          configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataAW.data = rs;
          dataAW.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);
          // console.log("dataAW.paginate ", dataAW.paginate);
          cash("#loadingDMaff").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDMaff").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataAW.page = p;
      getData();
    };

    const nextPage = () => {
      dataAW.page = dataAW.page + 1;
      getData();
    };

    const beforePage = () => {
      dataAW.page = dataAW.page - 1;
      getData();
    };

    const lastPage = () => {
      dataAW.page = dataAW.paginate.totalPages;
      getData();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataAW,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listKeyDS,
    };
  },
  data() {
    return {
      exDataAW: {
        id: "",
        pin: "",
        amount: "",
        bank: "",
      },
      listBank: [],
    };
  },
  methods: {
    async emptyField() {
      const ex = this.exDataAW;
      ex.id = "0";
      ex.pin = "";
      ex.amount = "";
      ex.bank = "";
    },
    ctc(f: string) {
      const ex: any = this.exDataAW;
      ex[f] = gpf.ctc(ex[f]);
    },
    async addAW() {
      gpf.gLoading.show();

      await this.emptyField();
      const lb = await api.getAkunBank();
      this.listBank = lb;
      this.exDataAW.bank = lb[0].id ? lb[0].id : "";
      console.log("listBank ", this.listBank);
      gpf.showModal("#modal-data-aw");

      gpf.gLoading.hide();
    },
    addAWApi() {
      gpf.gLoading.show();
      const ex = this.exDataAW;
      const params = new URLSearchParams();
      params.append("pin", ex.pin);
      params.append("amount", gpf.btc(ex.amount));
      params.append("bank", ex.bank);

      axios
        .post(api.ownerWithdrawal, params, this.configXwfu)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const ds = result.data.diagnostic;
          // console.log("diagnpstic add deu ", ds);

          await gpf.msgHandler(true, "Good Job!", ds.message);
          await gpf.hideModal("#modal-data-aw");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          await gpf.handleJwtExpired(error);
          await gpf.handleEds(error);
        });
    },
  },
});
