
import { defineComponent, ref, reactive } from "vue";
import { gpf } from "@/utils/global-functions";
import { helper as $h} from "@/utils/helper";
import { api } from "@/utils/api-config";
import axios from "axios";
import { dataTipeLB } from "./leaderboard";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const configGet = api.configGet();
    const configXwfu = api.configXwfu();
    const listTipe = dataTipeLB();

    const titleTable: Array<string> = [
      "Photo",
      "Name",
      "Jumlah",
    ];

    const dataLB = reactive({
      tempData: [] as any,
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      tipe: "affiliate",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
        dataPaginate: [],
      } as any,
      dateRange: "",
    });

    const date = new Date();
    const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    dataLB.dateRange = `${$h.formatDate(firstDay.toString(), 'DD MMM, YYYY')} - ${$h.formatDate(lastDay.toString(), 'DD MMM, YYYY')}`;

    const getDataPagination = async (pg?:1) => {
      const page = pg !== 1 ? dataLB.page : pg;
      const pageSize = dataLB.pageSize;
      dataLB.paginate = $h.paginationLocal(dataLB.tempData.length, page, pageSize, 3, dataLB.tempData);
      dataLB.data = dataLB.paginate.dataPaginate;
    };

    const getData = async () => {
      cash("#loadingDataLB").show();

      const since = $h.formatDate(dataLB.dateRange.split('-')[0], 'YYYY-MM-DD');
      const until = $h.formatDate(dataLB.dateRange.split('-')[1], 'YYYY-MM-DD');
      const tipe = dataLB.tipe;

      axios
        .get(
          api.affLeaderboard +
          "?since=" +
          since +
          "&until=" +
          until +
          "&tipe=" +
          tipe
          , configGet
        )
        .then(async (result: any) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          // console.log("response get du ", rs);

          dataLB.tempData = rs;
          await getDataPagination(1);
          // console.log("dataLB.paginate ", dataLB.paginate);
          cash("#loadingDataLB").hide();
        })
        .catch(async (error: any) => {
          cash("#loadingDataLB").hide();
          // console.log("response get du err ", error.response);
          await gpf.handleJwtExpired(error);
					await gpf.handleEds(error);
        });
    };
    getData();

    const changePS = () => {
      getDataPagination();
    };

    const goToPage = (p: number) => {
      dataLB.page = p;
      getDataPagination();
    };

    const nextPage = () => {
      dataLB.page = dataLB.page + 1;
      getDataPagination();
    };

    const beforePage = () => {
      dataLB.page = dataLB.page - 1;
      getDataPagination();
    };

    const lastPage = () => {
      dataLB.page = dataLB.paginate.totalPages;
      getDataPagination();
    };

    return {
      salesReportFilter,
      titleTable,
      getData,
      dataLB,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      configGet,
      configXwfu,
      listTipe,
    };
  },
  data() {
    return {};
  },
  methods: {
  },
});
